@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
*{margin:0px;padding:0px}html,body{font-family:"Open Sans",sans-serif;color:white;width:100%;height:100%;background-color:#222831}.homeScreen{width:100%;height:100%;background-color:#222831;display:flex;flex-flow:row wrap;justify-content:space-evenly;align-items:stretch;align-content:stretch}@media (min-width: 768px){.homeScreen{background-color:unset;background-color:green}}@media (min-width: 992px){.homeScreen{background-color:red}}@media (min-width: 1200px){.homeScreen{background-color:#222831}}.cardImagen{background-color:red;border-color:transparent;border-radius:2em;height:35rem;width:25rem;box-shadow:15px 15px 15px -3px #eeeeee09;background-repeat:no-repeat;background-size:cover;background-position:center;margin:15px auto 15px auto;padding:15px 15px 0px 0px;display:flex;flex-flow:column wrap;align-items:flex-end}.cardImagen>h4,.cardImagen p{color:#EEEEEE;font-weight:bold;font-size:large;background-color:rgba(26,25,25,0.698);border-radius:3px;margin-top:5px;padding:5px}.cardImagen>svg{width:50px;height:50px;transition:all 0.6s;cursor:pointer;padding-top:5px;border-bottom:none}.cardImagen>svg:hover{width:60px;height:60px;box-shadow:49px 15px 61px 54px rgba(0,0,0,0.1);margin-top:25px;border-radius:50px;border-bottom:#00ADB5 solid 1px}@media (min-width: 768px){.cardImagen{height:29rem;width:25rem}}@media (min-width: 992px){.cardImagen{height:40rem;width:32rem}}

